import 'react-native-gesture-handler';
import * as React from 'react';
import { Text, View } from "react-native";
import styles from '../Style/style'

class About extends React.Component {
  render() {
    return (
      <View style={styles.container}>
        <Text style={styles.titlePage}>Descrio:</Text>
        <Text style={styles.desc}>O ORWay  um aplicativo com o intuito de proporcionar que qualquer pessoa seja capaz de resolver problemas que envolvam o melhor uso de tarefas, ou seja, otimizao, atravs da metodologia Wizard.
        Para isso, o usurio no necessita ter conhecimento prvio sobre as tcnicas envolvidas na resoluo. A aplicao conta com uma arquitetura expansvel, ARC-NEOS, que  uma continuao do trabalho realizado em 2017 por Joo Paulo Lemos Rodrigues, NEOS.
      A ideia da arquitetura  acoplar diferentes modelos de resoluo para as categorias apresentadas de problemas de Pesquisa Operacional.</Text>
      </View>
    );
  }
}

export default About;  